html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  font-family: "Gilroy", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  color: #1f2023;
  background-color: #ffffff;
}

body[data-color-theme=dark] {
  background-color: #1f2023;
  color: #ffffff;
}

a {
  color: #1f2023;
  text-decoration: none;
}

[data-color-theme=dark] a {
  color: #ffffff;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

[data-color-theme=dark] input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #000000;
  -webkit-text-fill-color: #ffffff;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1445px;
  margin: 0 auto;
  padding: 0 250px;
}
@media (max-width: 1279px) {
  .container {
    padding: 0 100px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 29px;
  }
}

[data-tabs=parent] {
  overflow: hidden;
}
[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  background-color: #ffffff;
}

.header.is-opened {
  bottom: 0;
  overflow-y: auto;
}

[data-color-theme=dark] .header {
  background-color: #1f2023;
}

.footer__text {
  margin: 0;
  margin-bottom: 25px;
  min-height: 22px;
}
.footer__email {
  display: inline-block;
  max-width: 200px;
  margin: 0 auto;
  margin-bottom: 35px;
  padding: 12px 28px;
  border-radius: 25px;
  background-color: #1f2023;
  color: #ffffff;
  font-weight: 700;
  transition: opacity 0.3s ease;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .footer__email:hover, .footer__email:focus {
    opacity: 0.8;
  }
}
.footer__links {
  display: inline-flex;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}
.footer__link {
  display: block;
  transition: opacity 0.3s ease;
}
.footer__link svg {
  color: #1f2023;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .footer__link:hover, .footer__link:focus {
    opacity: 0.8;
  }
}
.footer__link:not(:last-child) {
  margin-right: 60px;
}

[data-color-theme=dark] .footer .footer__email {
  color: #1f2023;
  background-color: #ffffff;
}
[data-color-theme=dark] .footer .footer__link svg {
  color: #ffffff;
}

.titles {
