  font-size: 47px;
  line-height: 59px;
}
@media (max-width: 1023px) {
  h1.titles {
    font-size: 36px;
    line-height: 42px;
  }
}
@media (max-width: 767px) {
  h1.titles {
    margin-right: 40px;
    font-size: 28px;
