}

.navigation-changer {
  border-bottom: 1px solid #1f2023;
  min-height: 79px;
}
@media (max-width: 767px) {
  .navigation-changer {
    padding: 30px 0 30px 0;
    min-height: initial;
  }
}
@media (max-width: 374px) {
  .navigation-changer {
    padding: 25px 0 20px 0;
  }
}
.navigation-changer__menu {
  padding: 22px 0 26px 0;
}
@media (max-width: 767px) {
  .navigation-changer__menu {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    height: 0;
    padding: 0;
    transition: height 0.3s ease-out;
  }
}
.navigation-changer__list {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .navigation-changer__list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-bottom: 50px;
    font-size: 24px;
    line-height: 32px;
  }
  .navigation-changer__list li + li {
    margin-top: 50px;
  }
}
@media (max-width: 374px) {
  .navigation-changer__list {
    margin-bottom: 30px;
  }
  .navigation-changer__list li + li {
    margin-top: 30px;
  }
}
.navigation-changer__link {
  position: relative;
  display: flex;
  padding: 3px 0 5px 0;
  font-weight: 700;
  color: #828282;
  transition: color 0.6s ease;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .navigation-changer__link:hover, .navigation-changer__link:focus {
    color: rgba(31, 32, 35, 0.7);
  }
}
.navigation-changer__link.is-active {
  color: #1f2023;
  pointer-events: none;
}
.navigation-changer__link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 100%;
  height: 2px;
  background-color: #1f2023;
  transform: translateX(-50%) scaleX(0);
  transform-origin: right;
  transition: transform 0.6s ease;
}
.navigation-changer__link.is-active::before {
  transform: translateX(-50%) scaleX(1);
  transform-origin: left;
}
.navigation-changer__link.is-inactive::before {
  display: none;
}
.navigation-changer__link.is-inactive {
  color: #1f2023;
  pointer-events: none;
}
.navigation-changer__wrapper-toggle {
  display: none;
  justify-content: space-between;
  width: 100%;
}
@media (max-width: 767px) {
  .navigation-changer__wrapper-toggle {
    display: flex;
  }
}
.navigation-changer__toggle {
  position: relative;
  display: flex;
  flex-shrink: 0;
  width: 36px;
  height: 36px;
  padding: 4px;
  border: none;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
  transform: translateX(9px);
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .navigation-changer__toggle:hover, .navigation-changer__toggle:focus {
    opacity: 0.8;
  }
}
.navigation-changer__burger-line {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 18px;
  height: 2px;
  background-color: #1f2023;
  transform: translate(-50%, -50%);
}
.navigation-changer__burger-line--top {
  transition: translate 0.6s ease;
  transform: translate(-50%, calc(-50% - 6px));
}
.navigation-changer__burger-line--bottom {
  transition: translate 0.6s ease;
  transform: translate(-50%, calc(-50% + 6px));
}

@media (max-width: 767px) {
  .is-opened .navigation-changer {
    border: none;
    padding-bottom: 30px;
  }
  .is-opened .navigation-changer__item {
    margin-right: auto;
  }
  .is-opened .navigation-changer__wrapper-toggle {
    margin-bottom: 60px;
  }
  .is-opened .navigation-changer__toggle {
    align-items: center;
  }
  .is-opened .navigation-changer__burger-line--top {
    transform: translate(-50%, -50%) rotate(45deg);
    animation: open-menu-top 0.6s ease;
  }
  .is-opened .navigation-changer__burger-line--center {
    opacity: 0;
  }
  .is-opened .navigation-changer__burger-line--bottom {
    transform: translate(-50%, -50%) rotate(-45deg);
    animation: open-menu-bottom 0.6s ease;
  }
}
@media (max-width: 374px) {
  .is-opened .navigation-changer__wrapper-toggle {
    margin-bottom: 30px;
  }
}

@media (max-width: 767px) {
  .is-closed .navigation-changer__burger-line--top {
    animation: close-menu-top 0.6s ease;
  }
  .is-closed .navigation-changer__burger-line--center {
    transition: opacity 0.6s ease 0.3s;
  }
  .is-closed .navigation-changer__burger-line--bottom {
    animation: close-menu-bottom 0.6s ease;
  }
}

[data-color-theme=dark] .navigation-changer {
  border-color: #ffffff;
}
[data-color-theme=dark] .navigation-changer .navigation-changer__link {
  color: #a9a9a9;
}
[data-color-theme=dark] .navigation-changer .navigation-changer__link.is-active {
  color: #ffffff;
}
[data-color-theme=dark] .navigation-changer .navigation-changer__link::before {
